import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { DomSanitizer, SafeResourceUrl, SafeUrl } from '@angular/platform-browser';
/**
 * Generated class for the SueimiangPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({ segment: 'sueimiang', name: 'sueimiang' })
@Component({
  selector: 'page-sueimiang',
  templateUrl: 'sueimiang.html',
})
export class SueimiangPage {

  shuimianUrl: SafeUrl =  this._sanitizer.bypassSecurityTrustResourceUrl('');

  constructor(
    public navCtrl: NavController, 
    private _sanitizer: DomSanitizer,
    public navParams: NavParams) {
      
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SueimiangPage');
    this.shuimianUrl = this._sanitizer.bypassSecurityTrustResourceUrl(`http://zhijiaiot.com/sleep/index.html`);
  }

}



